import React, { useState } from 'react';
import EventManager from "../../../EventManager";
import { BrowserRouter as Router, Route, NavLink, Switch } from "react-router-dom";
import RoulettePro from 'react-roulette-pro';
import 'react-roulette-pro/dist/index.css';




import classNames from 'classnames/bind'
import styles from '../css/vongquay.module.css'

let cx = classNames.bind(styles);



const Vongquay = ({ generalData }) => {
    const prizes = [
        {
            id: '1',
            image: 'https://grandcaptcha.com/images/containers/93.png',
        },
        {
            id: '2',
            image: 'https://grandcaptcha.com/images/containers/22.png',
        },
        {
            id: '3',
            image: 'https://grandcaptcha.com/images/containers/13.png',
        },
        {
            id: '4',
            image: 'https://grandcaptcha.com/images/containers/72.png',
        },
        {
            id: '5',
            image: 'https://grandcaptcha.com/images/other_items/222.png',
        },
    ];

    const reproductionArray = (array = [], length = 0) => [
        ...Array(length)
            .fill('_')
            .map(() => array[Math.floor(Math.random() * array.length)]),
    ];

    const prizeList = [
        ...prizes,
        ...reproductionArray(prizes, prizes.length * 3),
        ...prizes,
        ...reproductionArray(prizes, prizes.length),
    ];
    const [start, setStart] = useState(false);

    const prizeIndex = prizes.length * 4 + 2;

    const handleStart = () => {
        setStart((prevState) => !prevState);
    };

    const handlePrizeDefined = () => {
        alert('🥳 Prize defined! 🥳');
    };

    return (
        <div className={cx('vongquay-box')}>
            <div className={cx('box')} >
                <div className={cx('box-new')} >
                    <RoulettePro
                        prizes={prizeList}
                        prizeIndex={prizeIndex}
                        start={start}
                        onPrizeDefined={handlePrizeDefined}
                    />
                    <button className={cx('btn-vongquay')} onClick={handleStart}>Start</button>
                </div>


            </div>

        </div>

    )
}
export default Vongquay
