import React from 'react';
import './css/hud.css'
import './css/menu.css'
import './css/main.css'
import './css/player.css'
import './css/gps.css'

import Player from './components/Player';
import Car from './components/Car';
import Gps from './components/Gps';
import Watch from './components/Watch';
import Logo from './components/Logo';
import MainMenu from './components/MainMenu';
import EventManager from "../../EventManager";
import Chat from './components/Chat';
import Notification from './components/Notification/Notification'


import { HudContext } from './context/HudContext'

class Hud extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
            allowDraggable: false
        }
    }

    componentDidCatch(error, errorInfo) {
        console.log(error, errorInfo);
        try {
            mp.trigger('client:ui:debug', 'Hud.jsx', error, errorInfo); // eslint-disable-line
        }
        catch (e) {

        }
    }

    componentDidMount() {
        EventManager.addHandler('hud', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            } else if (value.type === 'showEdit') {
                this.setState({ allowDraggable: true })
            } else if (value.type === 'hideEdit') {
                this.setState({ allowDraggable: false })
            } else return;
        })
    }

    componentWillMount() {
        window.HudComponents = {}
    }

    componentWillUnmount() {
        EventManager.removeHandler('hud');
    }

    render() {
        if (!this.state.show) {
            return null;
        }
        return (
            <React.Fragment>
                <HudContext.Provider value={{ allowDraggable: this.state.allowDraggable }}>
                    <div className="hud-main">
                        <div className='left'>

                            <div className="left-top">

                                <Chat />
                            </div>
                            <div className="left-bottom">
                                <div className="player-hud">
                                    <Player />
                                    <Gps />
                                </div>
                            </div>

                        </div>
                        <div className="right">
                            <MainMenu />
                            <div className="right-top">
                                <Logo />
                            </div>
                            <div className='hint'>
                                <p><span style={{ paddingLeft: 15, paddingBottom: 12, paddingTop: 12, paddingRight: 15 }} className='icona'>I</span> <span>Túi đồ</span></p>
                                <p><span className='icon'>M</span> <span>Menu nhân vật</span></p>
                                <p><span className='icon'>O</span> <span>Điện thoại</span></p>
                                <p><span className='icon'>G</span> <span>Tương tác</span></p>
                                <p><span className='icon'>Z</span> <span>Phónng to MAP</span></p>
                                <p><span className='icon'>B</span> <span>Khởi động phương tiện</span></p>

                            </div>
                            <div className="right-bottom">
                                <Car />

                            </div>

                        </div>
                    </div>
                </HudContext.Provider>
            </React.Fragment>
        )
    }
}

export default Hud;
