import React from 'react';
import EventManager from "../../../EventManager";
import { BrowserRouter as Router, Route, NavLink, Switch } from "react-router-dom";
import classNames from 'classnames/bind'
import styles from '../css/container.module.css'
import { Player, ControlBar } from 'video-react';
import "../../../../node_modules/video-react/dist/video-react.css";
let cx = classNames.bind(styles);

const Container = ({ generalData }) => {

    return (
        <div className={cx('container-box')}>
            <NavLink to='/case1' className={cx('box')} >
                <div className={cx('img')}>
                    <img src={`https://majestic-web-thanhhaicute.vercel.app/img/container/8.png`} />
                </div>
                <p>HÒM FERARRI </p>

            </NavLink>
            <NavLink to='/case2' className={cx('box')}>
                <div className={cx('img')}>
                    <img src={`https://majestic-web-thanhhaicute.vercel.app/img/container/1.png`} />
                </div>
                <p>HÒM BUGATI </p>

            </NavLink>
        </div>

    )
}
export default Container
