
import React from 'react';

import './css/tree.css'
import EventManager from "../../EventManager";
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";



class Tree extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
            visible: false,

        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Tree.jsx', error, errorInfo); // eslint-disable-line
    }

    componentDidMount() {
        EventManager.addHandler('Tree', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            }
        })
    }

    componentWillUnmount() {
        EventManager.removeHandler('Tree');
    }

    handleExit(status) {
        try {
            mp.trigger('client:Tree:closeTree', status); // eslint-disable-line
        }
        catch (e) {
            console.log(e);
        }
    }
    getJob() {
        try {
            mp.trigger('client:Tree:getJob'); // eslint-disable-line

        } catch (e) {
            // console.log(`Nghỉ việc`)
        }
    };

    render() {
        if (!this.state.show) {
            return null;
        }
        return (
            <Router>
                <div className='container-tree'>
                   
                    <div className="leftbox-tree">
                        <div className='title-job-tree'>
                                <img src={`https://thumbs.dreamstime.com/b/red-lawn-mower-cutting-grass-gardening-process-mowing-lawnmower-trimming-green-work-tool-equipment-summer-care-machine-backyard-152129336.jpg`}/>
                                <h3>Công việc</h3>
                                <p>Người cắt cỏ</p>

                        </div>

                 
                    </div>
                    <div className="box-getjobtree">
                        <div className='people-tree'>
                            <img src={`https://majestic-rp.ru/img/Home/about/person.webp`}/>
                        </div>
                        <div className="description">
                            <h3>Yêu cầu công việc</h3>
                            <span>Cấp độ 1</span>
                            <span>Có thẻ xanh</span>
                        </div>
                       <div className="description-job">
                            <h3>Miêu tả:</h3>
                            <p>Để bắt đầu công việc bạn cần phải nhận việc trước. Đây là công việc tương đối nhẹ nhàng dành cho các 
                                bạn mới tham gia máy chủ. Công việc này đòi hỏi bạn phải kiên nhẫn cắt từng bụi cỏ. Có 3 công cụ để 
                                cắt cỏ, bạn sẽ lấy từng công cụ ứng với từng màu của checkpoint. Lương sẽ được chuyển về tài khoản ngân hàng của bạn.
                                Trên map có từng chỗ để bạn là việc. hãy chọn 1 chỗ để bắt đầu công việc. Chúc bạn may mắn
                            </p>
                        </div>
                        <div className='btn-getjob'>
                            <button onClick={() => this.getJob()}>Nhận việc</button>
                        </div>
                        <div className='btn-exit'>
                            <span onClick={(status) => this.handleExit(status)}>Đóng</span>
                        </div> 
                       
                    </div>
                   {/* <div className='btn-getjob'>
                            <button onClick={() => this.getJob()}>Nhận việc</button>
                        </div>
                     <div className='btn-exit'>
                        <button onClick={(status) => this.handleExit(status)}>Đóng</button>
                    </div> */}

                </div>

            </Router>
        )
    }
}

export default Tree;
