
import React from 'react';
import EventManager from "../../../EventManager";
import { BrowserRouter as Router, Route, NavLink, Switch } from "react-router-dom";
import classNames from 'classnames/bind'
import styles from './css/ShopAlc.module.css'

let cx = classNames.bind(styles);

class ShopAlc extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
            money: 123333,
            bank: 1000,

        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'ShopAlc.jsx', error, errorInfo); // eslint-disable-line
    }
    componentDidMount() {
        EventManager.addHandler('ShopAlc', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            } else if (value.type === "updateValues") {
                this.setState({ show: value.isShow });
                this.setState({ money: value.money });
                this.setState({ bank: value.bank });


            } else return;
        })
    }
    componentWillUnmount() {
        EventManager.removeHandler('ShopAlc');
    }

    handleExit(status) {
        try {
            mp.trigger('client:ShopAlc:close', status); // eslint-disable-line
        }
        catch (e) {
            console.log(e);
        }
    }
    muabiaamMoney() {
        mp.trigger('client:ShopAlc:muabiaamMoney'); // eslint-disable-line
    }
    muabiaamBank() {
        mp.trigger('client:ShopAlc:muabiaamBank'); // eslint-disable-line
    }

    muaruouhillMoney() {
        mp.trigger('client:ShopAlc:muaruouhillMoney'); // eslint-disable-line
    }
    muaruouhillBank() {
        mp.trigger('client:ShopAlc:muaruouhillBank'); // eslint-disable-line
    }
    muaruourumMoney() {
        mp.trigger('client:ShopAlc:muaruourumMoney'); // eslint-disable-line
    }
    muaruourumBank() {
        mp.trigger('client:ShopAlc:muaruourumBank'); // eslint-disable-line
    }

    muaruouconcacMoney() {
        mp.trigger('client:ShopAlc:muaruouconcacMoney'); // eslint-disable-line
    }
    muaruouconcacBank() {
        mp.trigger('client:ShopAlc:muaruouconcacBank'); // eslint-disable-line
    }

    render() {

        if (!this.state.show) {
            return null;
        }
        return (
            <Router>
                <div className={cx('container')}>
                    <div className={cx('box-store')}>
                        <div className={cx('header')}>
                            <div className={cx('navbar-left')}>
                                <div className={cx('logo')}>
                                    <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/banners/a_robs.png`} />
                                </div>
                                <NavLink to='/thucan' className={cx('nav')} activeClassName={cx('active')}><i className="fa-solid fa-burger"></i>Rượu</NavLink>
                            </div>
                            <div className={cx('navbar-right')}>
                                <div className={cx('money')}>
                                    <p>${this.state.money}<span><i className="fa-solid fa-money-check-dollar"></i></span></p>
                                    <p>${this.state.bank}<span><i className="fa-solid fa-money-check"></i></span></p>
                                </div>

                                <div className={cx('btn-exit')} onClick={(status) => this.handleExit(status)}>
                                    <p >THOÁT <span>ESC</span></p>
                                </div>
                            </div>
                        </div>

                        <div className={cx('body')}>
                            <Switch>
                                <Route path='/thucan'>
                                    <div className={cx('card')}>
                                        <div className={cx('img')}>
                                            <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/icons/components/inventory/img/all-items/Item_243.png`} />
                                        </div>
                                        <div className={cx('line')}></div>
                                        <div className={cx('name')}>Chai bia A.M</div>
                                        <div className={cx('des')}>Bias này bạn có thể bổ sung độ khát<p></p> của bạn giúp nhân vật không bị khát</div>
                                        <div className={cx('price')}>$500</div>
                                        <div className={cx('pay')}>
                                            <span onClick={() => this.muabiaamMoney()}>MUA BẰNG TIỀN MẶT</span>
                                            <span onClick={() => this.muabiaamBank()}>MUA BẰNG THẺ</span>
                                        </div>
                                    </div>

                                    <div className={cx('card')}>
                                        <div className={cx('img')}>
                                            <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/icons/components/inventory/img/all-items/Item_248.png`} />
                                        </div>
                                        <div className={cx('line')}></div>
                                        <div className={cx('name')}>Chai ruou vang du tru Rockford Hill</div>
                                        <div className={cx('des')}>Bias này bạn có thể bổ sung độ khát<p></p> của bạn giúp nhân vật không bị khát</div>
                                        <div className={cx('price')}>$600</div>
                                        <div className={cx('pay')}>
                                            <span onClick={() => this.muaruouhillMoney()}>MUA BẰNG TIỀN MẶT</span>
                                            <span onClick={() => this.muaruouhillBank()}>MUA BẰNG THẺ</span>
                                        </div>
                                    </div>
                                    <div className={cx('card')}>
                                        <div className={cx('img')}>
                                            <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/icons/components/inventory/img/all-items/Item_249.png`} />
                                        </div>
                                        <div className={cx('line')}></div>
                                        <div className={cx('name')}>Chai ruou rum Ragga</div>
                                        <div className={cx('des')}>Bias này bạn có thể bổ sung độ khát<p></p> của bạn giúp nhân vật không bị khát</div>
                                        <div className={cx('price')}>$700</div>
                                        <div className={cx('pay')}>
                                            <span onClick={() => this.muaruourumMoney()}>MUA BẰNG TIỀN MẶT</span>
                                            <span onClick={() => this.muaruourumBank()}>MUA BẰNG THẺ</span>
                                        </div>
                                    </div>
                                    <div className={cx('card')}>
                                        <div className={cx('img')}>
                                            <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/icons/components/inventory/img/all-items/Item_250.png`} />
                                        </div>
                                        <div className={cx('line')}></div>
                                        <div className={cx('name')}>Chai ruou cognac Bourgeoix</div>
                                        <div className={cx('des')}>Bias này bạn có thể bổ sung độ khát<p></p> của bạn giúp nhân vật không bị khát</div>
                                        <div className={cx('price')}>$800</div>
                                        <div className={cx('pay')}>
                                            <span onClick={() => this.muaruouconcacMoney()}>MUA BẰNG TIỀN MẶT</span>
                                            <span onClick={() => this.muaruouconcacBank()}>MUA BẰNG THẺ</span>
                                        </div>
                                    </div>
                                </Route>

                            </Switch>
                        </div>
                    </div>
                </div>
            </Router>


        )
    }
}

export default ShopAlc;
