import React from 'react'
import './css/main.css'
import DialogButton from './components/DialogButton';
import EventManager from "../../../EventManager";

class Dialog extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
            name: 'Thư ký',
            subtitle: 'Nhân viên Tòa thị chính',
            qustion: 'Xin chào, tôi tên là Họ, tôi có thể giúp gì được không?ertetertetertertertertwfsgrrdfg ',
            buttons: [
                {
                    text: 'Tôi muốn tìm hiểu công việc này',
                    bgcolor: '',
                    params: {}
                },
                {
                    text: 'Nhận làm với công việc người làm vườn ở cấp độ 1',
                    bgcolor: '',
                    params: {}
                },
                {
                    text: 'Nghỉ việc',
                    bgcolor: '',
                    params: {}
                },
                {
                    text: 'Thoát',
                    bgcolor: '',
                    params: {}
                }
            ]
        }
    }

    componentDidMount() {
        EventManager.addHandler('ndialog', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            } else if (value.type === 'updateItems') {
                try {
                    this.setState({ show: true })
                    this.setState({ name: value.name })
                    this.setState({ subtitle: value.subtitle })
                    this.setState({ qustion: value.qustion })
                    this.setState({ buttons: value.buttons })
                }
                catch (e) {

                }
            }
        })
    }

    componentWillUnmount() {
        EventManager.removeHandler('ndialog');
    }

    render() {
        if (!this.state.show) {
            return null;
        }
        return (
            <div className="dialog-container">
                <div className="dialog-content">

                    <span className="dialog-content__name">
                        {this.state.name}
                    </span>
                    <div className="line"></div>
                    <span className="dialog-content__subtitle">
                        {this.state.subtitle}
                    </span>
                    <span className="dialog-content__question">
                        <span className='text'> {this.state.qustion}</span>
                    </span>
                    <div className="dialog-content__buttons">
                        {this.state.buttons.map((item, index) => (
                            <DialogButton
                                text={item.text}
                                bgcolor={item.bgcolor}
                                params={item.params}
                            />
                        ))}
                    </div>
                </div>
            </div>
        )
    }
}

export default Dialog