import React from 'react';

import '../css/tax.css'
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";
class Tax extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: true,
            visible: false,

        }
    }



    render() {
        if (!this.state.show) {
            return null;
        }
        return (
            <Router>
                <React.Fragment>
                    <span>Tax</span>
                </React.Fragment>
            </Router>

        )
    }
}

export default Tax;
