import React from 'react';
import EventManager from "../../../EventManager";

import { CircularProgressbarWithChildren } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";

import Draggable from '../Draggable'


class Player extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: true,
            microphone: true,
            drink: 5,
            eat: 60,
            wallet: "12.400.000$",
            card: "12.400$",
            color: '#48B9F2',
            background: 0.5,
            temp: 12,

        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Player.jsx', error, errorInfo); // eslint-disable-line
    }

    componentDidMount() {
        EventManager.addHandler('hudp', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            } else if (value.type === 'updateValues') {
                this.setState({ microphone: value.microphone });
                this.setState({ drink: value.drink });
                this.setState({ eat: value.eat });
                this.setState({ wallet: value.wallet });
                this.setState({ card: value.card });
                this.setState({ temp: value.temp });

                this.setState({ background: value.background });
            } else return;
        })
    }

    componentWillUnmount() {
        EventManager.removeHandler('hudp');
    }

    formatCurrency(num) {
        return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ')
    }
    formatMoney(string) {
        const temp = string.split(".");
        temp.pop();
        return temp.join(" ");
    }

    render() {
        const wallet = this.formatMoney(this.state.wallet);
        const card = this.formatMoney(this.state.card);

        const eatStyles = this.state.eat < 20 ? { path: { stroke: "crimson", } } : { path: { stroke: "lightsalmon" } };
        const drinkStyles = this.state.drink < 20 ? { path: { stroke: "crimson", } } : { path: { stroke: "skyblue" } };

        if (!this.state.show) {
            return null;
        }
        return (
            <React.Fragment>
                <div className="player-hud">
                    <div id="player-money" className="phud-money">
                        <div className="money-box">
                            <div className="wallet-text">
                                <i class="fa-solid fa-dollar-sign"></i>
                                <span style={{ marginLeft: 10 }}>{wallet}</span>
                            </div>
                        </div>
                        <div className="money-box">
                            <div className="credit-text">
                                <i className="fa-solid fa-building-columns"></i>
                                <span style={{ marginLeft: 10 }}>{card}</span>
                            </div>
                        </div>
                        <div className="money-box" style={{ fontSize: 15, marginTop: 10 }}>
                            <div className="credit-text">
                                <i style={{ color: "gray" }} className="fa-solid fa-temperature-high"></i>
                                <span style={{ marginLeft: 10, color: "gray" }}>{this.state.temp}</span>
                            </div>
                        </div>
                    </div>

                    <div id="player-needs" className="phud-needs">

                        <div className="needs-drink">
                            <CircularProgressbarWithChildren
                                value={this.state.drink}
                                maxValue={100}
                                styles={drinkStyles}
                            >
                                <img src={`https://findicons.com/files/icons/1049/2s_space_emotions_v2/256/thirst.png`} width="20" style={{ filter: this.state.drink <= 20 ? 'invert(57%) sepia(78%) saturate(6251%) hue-rotate(337deg) brightness(133%) contrast(113%)' : 'none' }} />
                            </CircularProgressbarWithChildren>
                        </div>
                        <div className="needs-eat">
                            <CircularProgressbarWithChildren
                                value={this.state.eat}
                                maxValue={100}
                                styles={eatStyles}
                            >
                                <img src={`https://cdn-icons-png.flaticon.com/512/742/742880.png`} style={{ filter: this.state.eat <= 20 ? 'invert(57%) sepia(78%) saturate(6251%) hue-rotate(337deg) brightness(133%) contrast(113%)' : 'none' }} width="20" />
                            </CircularProgressbarWithChildren>
                        </div>
                        <div id="player-mic" className="phud_mic">
                            {this.state.microphone ? <i class="fa-solid fa-microphone"></i> : <i class="fa-solid fa-microphone-slash"></i>}
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default Player;
