import React from "react";
import EventManager from "../../../EventManager";
import Draggable from "../Draggable";
import ChangingProgressProvider from "./ChangingProgressProvider";

import {
  CircularProgressbar,
  CircularProgressbarWithChildren,
  buildStyles
} from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import ReactProgressLineBar from 'react-progress-line-bar'
class Car extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      isShowSmall: false,
      light: false,
      door: false,
      engine: false,
      turnLeft: false,
      turnRight: false,
      fuel: 50,
      fuelType: "L",
      max_fuel: 100, // Максимальная вместимость топливного бака
      speed: 20,
      speedLabel: "km/h",
      carname: "Italirsx",
      deg: -45,
      color: "#48B9F2",
      background: 0.5,
      s_km: 4.70,
      vEngine: 90,
      showRadar: false,
      radarRearSpeed: "",
      radarRearSpeedMax: "",
      radarFrontSpeed: "",
      radarFrontSpeedMax: "",
      radarPatrolSpeed: "",

      style: {
        trail: {
          strokeLinecap: "butt",
          transform: "rotate(-126deg)",
          transformOrigin: "center center",
        },
        path: {
          strokeLinecap: "butt",
          transform: "rotate(-126deg)",
          transformOrigin: "center center",
          stroke: "green",
        },
        text: {
          fill: "white",
        },
      },
    };
  }

  componentDidCatch(error, errorInfo) {
    mp.trigger("client:ui:debug", "Car.jsx", error, errorInfo); // eslint-disable-line
  }

  componentDidMount() {
    this.setState({ show: false });

    EventManager.addHandler("hudc", (value) => {
      if (value.type === "show") {
        this.setState({ show: true });
      } else if (value.type === "hide") {
        this.setState({ show: false });
      } else if (value.type === "updateValues") {
        this.setState({ show: value.isShow });
        this.setState({ isShowSmall: value.isShowSmall });
        this.setState({ light: value.light });
        this.setState({ door: value.door });
        this.setState({ engine: value.engine });
        this.setState({ fuel: value.fuel });
        this.setState({ fuelType: value.fuelType });
        this.setState({ s_km: value.s_km });
        this.setState({ max_fuel: value.max_fuel });
        this.setState({ vEngine: value.vEngine });

        this.setState({ speed: value.speed });
        this.setState({ speedLabel: value.speedLabel });
        this.setState({ background: value.background });
        this.setState({ carname: value.carname });
        this.setState({ turnLeft: value.turnLeft });
        this.setState({ turnRight: value.turnRight });
      } else if (value.type === "updateRadarValues") {
        this.setState({ showRadar: value.showRadar });
        this.setState({ radarRearSpeed: value.radarRearSpeed });
        this.setState({ radarRearSpeedMax: value.radarRearSpeedMax });
        this.setState({ radarFrontSpeed: value.radarFrontSpeed });
        this.setState({ radarFrontSpeedMax: value.radarFrontSpeedMax });
        this.setState({ radarPatrolSpeed: value.radarPatrolSpeed });
      } else return;
    });

    this.speed();

  }

  componentWillUnmount() {
    EventManager.removeHandler("hudc");
  }

  componentDidUpdate(prevProp, prevState) {
    if (this.state.speed !== prevState.speed) {
      this.speed();
    }
  }

  speed() {
    // Чуть позже добавлю перерисованный спидометр, ибо текущий с погрешностью

    let speedProcent = (this.state.speed / 400) * 100;
    let deg = 180 * (speedProcent / 100);
    this.setState({ deg: deg - 45 });
  }



  render() {
    let fuel_liner = (this.state.fuel * 100) / this.state.max_fuel;
    const speed_score = {
      transform: `rotate(${this.state.deg}deg)`,
      borderBottomColor: this.state.color,
      borderLeftColor: this.state.color,
    };

    const prop = {
      circleRatio: 0.2,
      strokeWidth: 2,
    };

    if (!this.state.show) {
      return null;
    }
    return (
      <React.Fragment>
        <div className="hud-speed" >
          <div className="box-speed">
            <div className="Circular">
              <ChangingProgressProvider values={this.state.speed}>
                {value => (
                  <CircularProgressbar className="CircularProgressbar"
                    value={this.state.speed}
                    maxValue={300}
                    circleRatio={0.75}
                    styles={buildStyles({
                      rotation: 1 / 3.2 + 1 / 3.2,
                      strokeLinecap: "butt",
                      trailColor: "#968d7c38",
                      textSize: '10px',
                      textColor: 'white',
                      pathColor: `white`,
                      transition: 'stroke-dashoffset 0.5s ease 0s',

                    })}
                  />)}
              </ChangingProgressProvider>
              <div className="congtomet">
                <span>{this.state.speed}</span>
              </div>
              <div className="iconkm">
                <span>{this.state.speedLabel}</span>
              </div>
              <div className="fuel">
                <div className="icon-fuel-img">
                  {
                    this.state.fuelType ? <i className="fa-solid fa-gas-pump"></i> : <i className="fa-solid fa-bolt"></i>
                  }
                </div>
                <span>{this.state.fuel}</span>
                <div className="line"></div>
                <span>{this.state.max_fuel}</span>
              </div>
              <div className="skm">
                <p>{this.state.s_km} <span>km</span></p>
              </div>
              <div className="carname">
                <p>{this.state.carname}</p>
              </div>
              <div className="engine">
                {
                  this.state.engine ? <i class="fa-solid fa-plug-circle-plus" style={{ color: "green" }}></i> :
                    <i class="fa-solid fa-plug-circle-plus" style={{ color: "red" }}></i>
                }
              </div>
              <div className="light">
                {
                  this.state.light ? <i class="fa-solid fa-lightbulb" style={{ color: "green" }}></i> :
                    <i class="fa-solid fa-lightbulb" style={{ color: "red" }}></i>
                }
              </div>
              <div className="door">
                {
                  this.state.door ? <i className="fa-solid fa-door-closed" style={{ color: "green" }}></i> :
                    <i className="fa-solid fa-door-closed" style={{ color: "red" }}></i>
                }
              </div>
              <div className="vengine">
                <i className="fa-solid fa-heart-pulse"></i>
                <p>{this.state.vEngine}%</p>
              </div>
            </div>


          </div>
        </div>


      </React.Fragment >
    );
  }
}

export default Car;
