import React from 'react';
import EventManager from "../../../EventManager";
import { BrowserRouter as Router, Route, NavLink, Switch } from "react-router-dom";
import classNames from 'classnames/bind'
import styles from '../css/bussiness.module.css'
import ButtonGps from './setting/ButtonGps'
let cx = classNames.bind(styles);

const Bussiness = ({ bussiness }) => {
    return (
        <div className={cx('bussiness')}>
            {bussiness.map((item, index) => (
                <div key={`business-list-` + index}>

                    <div className={cx('box')}>
                        <div className={cx('img')}>
                            <img src={item.img} />
                        </div>
                        <img
                            src={
                                item.type === 0 ? `https://majestic-web-thanhhaicute.vercel.app/ui/accountmenu/img/property__house__icon.svg` :
                                    (item.type === 1 ? `https://majestic-web-thanhhaicute.vercel.app/ui/accountmenu/img/property__case__icon.svg` : `https://majestic-web-thanhhaicute.vercel.app/ui/accountmenu/img/property__apart__icon.svg`)
                            }
                            className="accountmenu__content__cards__business__img"
                        />
                        <p>Tên cửa hàng: {item.title}</p>
                        <p>Giá: {item.price.replace(/\B(?=(\d{3})+(?!\d))/g, " ")}</p>
                        <p>Địa chỉ: {item.address}</p>
                        <p>Cửa: {item.doors}</p>
                        <ButtonGps filled={false} position="left" x={item.x} y={item.y} />

                    </div>
                </div>
            ))}

        </div >

    )
}
export default Bussiness
