import React from 'react';
import EventManager from "../../../EventManager";
import { Animated } from 'react-animated-css';

class Logo extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: true,
            showAmmo: false,
            showQuest: true,
            questTitle: 'Vòng quay may mắn',
            questText: `Bây giờ bạn có thể quay vòng quay may mắn tại Diamond Casino!`,
            questDesc: `Thời gian còn lại 5S`,
            ammoCount: 0,
            ammoMode: 'auto',
            questAnim: '',
            date: '01.01',
            time: '12:00',
            online: '0',
            max_player: '1000',
            id: '0',
            color: '#48B9F2',
            background: 0.5,
            questTitle1: 'Chơi đủ 20 giờ',
            questText1: `Nhận ngay 100.000$`,
        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Logo.jsx', error, errorInfo); // eslint-disable-line
    }

    componentDidMount() {
        EventManager.addHandler('hudl', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            } else if (value.type === 'updateValues') {
                this.setState({ date: value.date });
                this.setState({ time: value.time });
                this.setState({ online: value.online });
                this.setState({ max_player: value.max_player });
                this.setState({ id: value.id });
                this.setState({ showAmmo: value.showAmmo });
                this.setState({ ammoCount: value.ammoCount });
                this.setState({ ammoMode: value.ammoMode });
                this.setState({ background: value.background });
            } else if (value.type === 'updateQuest') {
                this.setState({ showQuest: value.showQuest });
                this.setState({ questTitle: value.questTitle });
                this.setState({ questText: value.questText });
                this.setState({ questDesc: value.questDesc });
            } else if (value.type === 'updateOnline') {
                this.setState({ showQuest: value.showQuest });
                this.setState({ questTitle1: value.questTitle1 });
                this.setState({ questText1: value.questText1 });
            }
            else if (value.type === 'updateQuestAnim') {
                this.setState({ questAnim: value.questAnim });
            } else return;
        })
    }

    componentWillUnmount() {
        EventManager.removeHandler('hudl');
    }

    render() {
        if (!this.state.show) {
            return null;
        }
        return (
            <React.Fragment>
                <div className="logo-main">
                    <div className='logo'>
                        <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/auth/logo.png`} />
                    </div>
                    <div className="logo-info">
                        <div className="logo-info__text-bold">{this.state.date}  {this.state.time}</div>
                        <span className="logo-info__text-normal">Online: {this.state.online}  <p
                            className="logo-info__text-bold">ID: {this.state.id}</p></span>
                    </div>

                    <div className="gun">
                        <div
                            className={this.state.showAmmo ? 'logo-ammo' : 'hide'}>
                            <div className={'logo-ammo-img ammo-' + this.state.ammoMode}></div>
                            {this.state.ammoCount}
                        </div>
                    </div>
                    <div className="quest">
                        <div className='questTitle'> {this.state.questTitle}  </div>
                        <div className='questText'> {this.state.questText}</div>
                        <div className='questDesc'> {this.state.questDesc}</div>
                    </div>
                    <div className="quest1">
                        <div className='questTitle'> {this.state.questTitle1}  </div>
                        <div className='questText'> {this.state.questText1}</div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default Logo;
