import React from 'react';
import EventManager from "../../../EventManager";
import { BrowserRouter as Router, Route, NavLink, Switch } from "react-router-dom";
import classNames from 'classnames/bind'
import styles from '../css/vehicle.module.css'
import ButtonGps from './setting/ButtonGps'

let cx = classNames.bind(styles);

const Vehicle = ({ propertyCars }) => {
    return (
        <div className={cx('vehicle')}>
            {propertyCars.map((item, key) => {
                return (
                    <div key={`car-list-` + key} className={cx('box')}>
                        <div className={cx('img')}>
                            <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/carsv/640/${item.name.toLowerCase()}.jpg`} />
                        </div>
                        <p className={cx('name-car')}>{item.name}</p>
                        <p className={cx('name-type')}>Loại :<span>{item.type}</span> </p>
                        <p className={cx('name-type')}>Thuế :<span>{item.vin}</span> </p>
                        <p className={cx('name-type')}>Độ bền :<span>{item.carclass}</span> </p>
                        <p className={cx('name-type')}>Nhiện liệu :<span>{item.def}</span> </p>
                        <p className={cx('name-number')}>Biển số :<span>{item.number}</span> </p>
                        <p className={cx('btn')}>
                            <ButtonGps filled={false} position="left" x={item.x} y={item.y} />
                        </p>
                    </div>
                )
            })}

        </div >

    )
}
export default Vehicle
