import React from 'react';
import EventManager from "../../../EventManager";

import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";
import classNames from 'classnames/bind'
import styles from '../css/auth.module.css'
import About from '../images/about.svg'
import Logo from '../images/logo.svg'
import Bg1 from '../images/bg1.svg'
import Resigter from './Resigter';

let cx = classNames.bind(styles);


class Authorization extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            showAuto: true,
            acceptRules: false,
            login: '',
            password: '',
            mailReg: '',
            loginReg: '',
            passwordReg: '',
            passwordRegCheck: '',
            pagePlayer: '',
            audioUrl: '',
            defaultLogin: '',
            modalrules: false,
        }
    };

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Authorization.jsx', error, errorInfo); // eslint-disable-line
    }

    handleKeyPress = (event) => {
        if (event.key === 'Enter') {
            if (this.state.showAuto) this.clickLogin();
            else this.clickReg();
        }
    }

    componentDidMount() {
        document.addEventListener("keydown", this.handleKeyPress);

        EventManager.addHandler('authMain:2', value => {
            if (value.type === 'login') {
                this.setState({ defaultLogin: value.login })

            } else return;
        })

    }


    componentWillUnmount() {
        document.removeEventListener("keydown", this.handleKeyPress);
        EventManager.removeHandler('authMain:2');
    }


    valueLogin(event) {
        this.setState({ login: event.target.value.replace(/[^a-zA-Z0-9]+/g, '') })
    };

    valuePassword(event) {
        this.setState({ password: event.target.value.replace(/[^a-zA-Z0-9]+/g, '') })
    };

    valueMailReg(event) {
        this.setState({ mailReg: event.target.value })
    };

    valueLoginReg(event) {
        this.setState({ loginReg: event.target.value.replace(/[^a-zA-Z0-9]+/g, '') })
    };

    valuePasswordReg(event) {
        this.setState({ passwordReg: event.target.value.replace(/[^a-zA-Z0-9]+/g, '') })
    };

    valuePasswordRegCheck(event) {
        this.setState({ passwordRegCheck: event.target.value.replace(/[^a-zA-Z0-9]+/g, '') })
    };



    clickLogin() {
        try {
            if (!this.state.login) {
                mp.trigger('client:user:auth:login', // eslint-disable-line
                    this.state.defaultLogin, this.state.password);
            }
            else {
                mp.trigger('client:user:auth:login', // eslint-disable-line
                    this.state.login, this.state.password);
            }
        } catch (e) {
            console.log(e);
        }
    };

    clickReg() {
        try {
            mp.trigger('client:user:auth:register', // eslint-disable-line
                this.state.mailReg, this.state.loginReg,
                this.state.passwordReg, this.state.passwordRegCheck,
                this.state.acceptRules);
        } catch (e) {
            console.log(e);
        }
    };



    acceptRules = () => {
        this.setState({
            acceptRules: !this.state.acceptRules
        });
    };
    clickCheckRules() {
        this.setState({
            modalrules: true,
        });
    }
    closeRules() {
        this.setState({
            modalrules: false,
        });
    }

    render() {
        return (
            <Router>
                <div className={cx('container')}>
                    <div className={cx('about')}>
                        <div className={cx('bg')}>
                            <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/auth/about.svg`} alt="about" />
                        </div>
                        <div className={cx('title-bg')}>
                            <span>Chào mừng bạn trở lại!</span>
                        </div>
                        <div className={cx('infomation')}>
                            <p>Chúng tôi rất vui khi được gặp bạn tại dự án Maryland RP. Chúc bạn chơi game vui vẻ!</p>
                            <div className={cx('link')}>
                                <span><i className="fa-solid fa-globe"></i>maryland-rp.vn</span>
                                <span><i className="fa-brands fa-discord"></i>discord.gg/maryland</span>
                                <span><i className="fa-brands fa-facebook"></i>fb.com/maryland.com</span>
                            </div>
                            <p className={cx('arlet')}>Nếu bạn không có con trỏ đích, hãy nhấn phím ~ (E)</p>
                        </div>
                        <Link to='/auth' className={cx('btn-select')}>
                            <button>PLAY</button>
                        </Link>
                    </div>
                </div>
                <Switch>
                    <Route exact path="/auth" >
                        <div className={cx('container-auth')}>
                            <div className={cx('auth')}>
                                <div className={cx('logo')}>
                                    <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/auth/logo.svg`} />
                                </div>
                                <div className={cx('title')}>
                                    <span>Chào mừng bạn đến với máy chủ.</span>
                                    <p>Đăng nhập bằng tài khoản của bạn hoặc đăng ký một tài khoản mới.</p>
                                </div>
                                <div className={cx('input-account')}>
                                    <input
                                        type='text'
                                        placeholder='Tài khoản'
                                        onChange={this.valueLogin.bind(this)}
                                        value={this.state.login}
                                        defaultValue={this.state.defaultLogin}
                                    />
                                </div>
                                <div className={cx('input-password')}>
                                    <input
                                        type='password'
                                        placeholder='Mật khẩu'
                                        value={this.state.password}
                                        onChange={this.valuePassword.bind(this)}
                                    />
                                </div>
                                <div className={cx('btn-login')}>
                                    <button onClick={this.clickLogin.bind(this)}>ĐĂNG NHẬP</button>
                                    <button>
                                        <Link to='/resigter'> ĐĂNG KÝ</Link>
                                    </button>

                                </div>
                            </div>
                        </div>

                    </Route>
                    <Route exact path="/resigter" >
                        <Resigter />

                    </Route>

                </Switch>

            </Router>
        )
    }
}

export default Authorization;
