
import React from 'react';
import EventManager from "../../../EventManager";
import { BrowserRouter as Router, Route, NavLink, Switch } from "react-router-dom";
import classNames from 'classnames/bind'
import styles from './css/menutaco.module.css'

let cx = classNames.bind(styles);

class Taco extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
            money: 123333,
            bank: 1000,

        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Taco.jsx', error, errorInfo); // eslint-disable-line
    }
    componentDidMount() {
        EventManager.addHandler('Taco', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            } else if (value.type === "updateValues") {
                this.setState({ show: value.isShow });
                this.setState({ money: value.money });
                this.setState({ bank: value.bank });


            } else return;
        })
    }
    componentWillUnmount() {
        EventManager.removeHandler('Taco');
    }

    handleExit(status) {
        try {
            mp.trigger('client:Taco:close', status); // eslint-disable-line
        }
        catch (e) {
            console.log(e);
        }
    }
    muasupMoney() {
        mp.trigger('client:taco:muaphomaimoney'); // eslint-disable-line
    }
    muasupBank() {
        mp.trigger('client:taco:muaphomaibank'); // eslint-disable-line
    }

    muasinggumMoney() {
        mp.trigger('client:taco:muasinggummoney'); // eslint-disable-line
    }
    muasinggumBank() {
        mp.trigger('client:taco:muasinggumbank'); // eslint-disable-line
    }
    muabapMoney() {
        mp.trigger('client:taco:muabapmoney'); // eslint-disable-line
    }
    muabapBank() {
        mp.trigger('client:taco:muabapbank'); // eslint-disable-line
    }

    muanuocsuoiMoney() {
        mp.trigger('client:taco:muanuocsuoiMoney'); // eslint-disable-line
    }
    muanuocsuoiBank() {
        mp.trigger('client:taco:muanuocsuoiBank'); // eslint-disable-line
    }

    render() {

        if (!this.state.show) {
            return null;
        }
        return (
            <Router>
                <div className={cx('container')}>
                    <div className={cx('box-store')}>
                        <div className={cx('header')}>
                            <div className={cx('navbar-left')}>
                                <div className={cx('logo')}>
                                    <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/banners/s_taco.png`} />
                                </div>
                                <NavLink to='/thucan' className={cx('nav')} activeClassName={cx('active')}><i className="fa-solid fa-burger"></i>Thực phẩm</NavLink>
                            </div>
                            <div className={cx('navbar-right')}>
                                <div className={cx('money')}>
                                    <p>${this.state.money}<span><i className="fa-solid fa-money-check-dollar"></i></span></p>
                                    <p>${this.state.bank}<span><i className="fa-solid fa-money-check"></i></span></p>
                                </div>

                                <div className={cx('btn-exit')} onClick={(status) => this.handleExit(status)}>
                                    <p >THOÁT <span>ESC</span></p>
                                </div>
                            </div>
                        </div>

                        <div className={cx('body')}>
                            <Switch>
                                <Route path='/thucan'>
                                    <div className={cx('card')}>
                                        <div className={cx('img')}>
                                            <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/icons/components/inventory/img/all-items/Item_11.png`} />
                                        </div>
                                        <div className={cx('line')}></div>
                                        <div className={cx('name')}>Pho mai Phat Chips</div>
                                        <div className={cx('des')}>Ăn 1 Pho mai Phat Chips này bạn có thể bổ sung độ no<p></p> của bạn giúp nhân vật không bị đói</div>
                                        <div className={cx('price')}>$200</div>
                                        <div className={cx('pay')}>
                                            <span onClick={() => this.muaphomaiMoney()}>MUA BẰNG TIỀN MẶT</span>
                                            <span onClick={() => this.muaphomaiBank()}>MUA BẰNG THẺ</span>
                                        </div>
                                    </div>

                                    <div className={cx('card')}>
                                        <div className={cx('img')}>
                                            <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/store/food/Item_14.png`} />
                                        </div>
                                        <div className={cx('line')}></div>
                                        <div className={cx('name')}>Singgum</div>
                                        <div className={cx('des')}>Ăn 1 Singgum này bạn có thể bổ sung độ no<p></p> của bạn giúp nhân vật không bị đói</div>
                                        <div className={cx('price')}>$30</div>
                                        <div className={cx('pay')}>
                                            <span onClick={() => this.muasinggumMoney()}>MUA BẰNG TIỀN MẶT</span>
                                            <span onClick={() => this.muasinggumBank()}>MUA BẰNG THẺ</span>
                                        </div>
                                    </div>
                                    <div className={cx('card')}>
                                        <div className={cx('img')}>
                                            <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/store/food/Item_20.png`} />
                                        </div>
                                        <div className={cx('line')}></div>
                                        <div className={cx('name')}>Bắp răng</div>
                                        <div className={cx('des')}>Ăn 1 bắp rang này bạn có thể bổ sung độ no<p></p> của bạn giúp nhân vật không bị đói</div>
                                        <div className={cx('price')}>$430</div>
                                        <div className={cx('pay')}>
                                            <span onClick={() => this.muabaprangMoney()}>MUA BẰNG TIỀN MẶT</span>
                                            <span onClick={() => this.muabaprangBank()}>MUA BẰNG THẺ</span>
                                        </div>
                                    </div>
                                    <div className={cx('card')}>
                                        <div className={cx('img')}>
                                            <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/store/food/Item_18.png`} />
                                        </div>
                                        <div className={cx('line')}></div>
                                        <div className={cx('name')}>Nước suối</div>
                                        <div className={cx('des')}>Giúp bù nước cho cơ thể của bạn.<p></p>Giúp nhân vật không bị khát</div>
                                        <div className={cx('price')}>$330</div>
                                        <div className={cx('pay')}>
                                            <span onClick={() => this.muanuocsuoiMoney()}>MUA BẰNG TIỀN MẶT</span>
                                            <span onClick={() => this.muanuocsuoiBank()}>MUA BẰNG THẺ</span>
                                        </div>
                                    </div>
                                </Route>

                            </Switch>
                        </div>
                    </div>
                </div>
            </Router>


        )
    }
}

export default Taco;
