import React from 'react';
import EventManager from "../../../EventManager";
import { BrowserRouter as Router, Route, NavLink, Switch } from "react-router-dom";
import classNames from 'classnames/bind'
import styles from '../css/house.module.css'
import ButtonGps from './setting/ButtonGps'

let cx = classNames.bind(styles);

const House = ({ propertyHouse }) => {
    return (
        <div className={cx('house')}>
            <div className={cx('box')}>
                <div className={cx('img')}>
                    <img src={`https://i.pinimg.com/originals/38/d6/2b/38d62b3936c113f5004a7c1ff1069246.jpg`} />
                    <p>Loại nhà :<span>{propertyHouse.type}</span></p>
                    <p>Tên nhà :<span>{propertyHouse.name}</span></p>
                    <p>Địa chỉ nhà :<span>{propertyHouse.address}</span></p>
                    <p>Thuế nhà :<span>{propertyHouse.doors}</span></p>
                    <p>Gara nhà :<span>{propertyHouse.carplace}</span></p>
                    <p>Số phòng :<span>{propertyHouse.roommate}</span></p>
                    <p>Giá bán mặc định :<span>{propertyHouse.gprice.replace(/\B(?=(\d{3})+(?!\d))/g, ".")} $</span></p>
                    <ButtonGps filled={false} position="left" x={propertyHouse.x} y={propertyHouse.y} />
                </div>
            </div>
        </div >

    )
}
export default House
