import React from 'react';
import EventManager from "../../../EventManager";

import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";
import classNames from 'classnames/bind'
import styles from '../css/resigter.module.css'
import About from '../images/about.svg'
import Logo from '../images/logo.svg'
import Bg1 from '../images/bg1.svg'
import Bg2 from '../images/bg2.svg'
import Authorization from './Authorization';

let cx = classNames.bind(styles);


class Resigter extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            showAuto: true,
            mailReg: '',
            loginReg: '',
            passwordReg: '',
            passwordRegCheck: '',
            pagePlayer: '',
            audioUrl: '',
            defaultLogin: '',
        }
    };

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Authorization.jsx', error, errorInfo); // eslint-disable-line
    }

    handleKeyPress = (event) => {
        if (event.key === 'Enter') {
            if (this.state.showAuto) this.clickLogin();
            else this.clickReg();
        }
    }

    componentDidMount() {
        document.addEventListener("keydown", this.handleKeyPress);

        EventManager.addHandler('authMain:2', value => {
            if (value.type === 'login') {
                this.setState({ defaultLogin: value.login })

            } else return;
        })

    }


    componentWillUnmount() {
        document.removeEventListener("keydown", this.handleKeyPress);
        EventManager.removeHandler('authMain:2');
    }




    valueMailReg(event) {
        this.setState({ mailReg: event.target.value })
    };

    valueLoginReg(event) {
        this.setState({ loginReg: event.target.value.replace(/[^a-zA-Z0-9]+/g, '') })
    };

    valuePasswordReg(event) {
        this.setState({ passwordReg: event.target.value.replace(/[^a-zA-Z0-9]+/g, '') })
    };

    valuePasswordRegCheck(event) {
        this.setState({ passwordRegCheck: event.target.value.replace(/[^a-zA-Z0-9]+/g, '') })
    };



    clickReg() {
        try {
            mp.trigger('client:user:auth:register', // eslint-disable-line
                this.state.mailReg, this.state.loginReg,
                this.state.passwordReg, this.state.passwordRegCheck,
                this.state.acceptRules);
        } catch (e) {
            console.log(e);
        }
    };


    render() {
        return (
            <Router>
                <div className={cx('container-auth')}>
                    <div className={cx('resigter')}>
                        <div className={cx('left')}>
                            <div className={cx('bg')}>
                                <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/auth/bg2.svg`} alt="about" />
                            </div>
                        </div>
                        <div className={cx('right')}>
                            <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/auth/logo.svg`} alt="about" style={{ width: 300 }} />
                            <div style={{ marginTop: 10, fontWeight: 400 }} >
                                <h3>Chào mừng bạn đến với máy chủ.</h3>
                                <h3>Bạn cần phải đăng ký.</h3>
                            </div>
                            <div className={cx('account')}>
                                <input
                                    type="text"
                                    placeholder='Nhập tài khoản'
                                    value={this.state.loginReg}
                                    onChange={this.valueLoginReg.bind(this)}
                                />

                            </div>
                            <div className={cx('email')}>
                                <input
                                    type="text"
                                    placeholder='Nhập email'
                                    onChange={this.valueMailReg.bind(this)}
                                />
                            </div>
                            <div className={cx('password')}>
                                <input
                                    type="password"
                                    placeholder='Nhập mật khẩu'
                                    value={this.state.passwordReg}
                                    onChange={this.valuePasswordReg.bind(this)}
                                />
                            </div>
                            <div className={cx('password')}>
                                <input
                                    type="password"
                                    placeholder='Nhập lại mật khẩu'
                                    value={this.state.passwordRegCheck}
                                    onChange={this.valuePasswordRegCheck.bind(this)}
                                />
                            </div>

                            <div className={cx('rule')} >
                                <p><input type="checkbox" id="acceptRules" /> <span>Bạn đồng ý tất cả quy tắc máy chủ</span></p>
                                <p><input type="checkbox" id="acceptRules" /> <span>Bạn cam kết không hack/cheats</span> </p>
                                <p><input type="checkbox" id="acceptRules" /> <span>Bạn cam kết đủ 18 tuổi</span></p>

                            </div>

                            <div className={cx('btn-resigter')} >
                                <button onClick={this.clickReg.bind(this)}>ĐĂNG KÝ</button>
                                <button> <Link to='/auth'>ĐĂNG NHẬP</Link></button>
                            </div>
                        </div>
                    </div>
                    <Switch>
                        <Route exact path="/auth" >
                            <Authorization />
                        </Route>
                    </Switch>
                </div>


            </Router>
        )
    }
}

export default Resigter;
