
import React from 'react';
import EventManager from "../../../src/EventManager";
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";
import classNames from 'classnames/bind'
import Logo from './images/logo.webp'
import styles from './css/menufuel.module.css'
import { Bar } from "react-chartjs-2";
import Chart from 'chart.js/auto';

let cx = classNames.bind(styles);

class MenuFuel extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
            giaXang: 134,
            giaDau: 100,
            giaDien: 45,
            giaXangMayBay: 245,
            street: "Town hill"
        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'MenuFuel.jsx', error, errorInfo); // eslint-disable-line
    }
    componentDidMount() {
        EventManager.addHandler('MenuFuel', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            } else if (value.type === "updateValues") {
                this.setState({ show: value.isShow });
                this.setState({ giaDau: value.giaDau });
                this.setState({ giaXang: value.giaXang });
                this.setState({ giaDien: value.giaDien });
                this.setState({ giaXangMayBay: value.giaXangMayBay });
                this.setState({ street: value.street });
            } else return;
        })
    }
    componentWillUnmount() {
        EventManager.removeHandler('MenuFuel');
    }

    handleExit(status) {
        try {
            mp.trigger('client:menufuel:close', status); // eslint-disable-line
        }
        catch (e) {
            console.log(e);
        }
    }
    handleFullLXang() {
        try {
            mp.trigger('client:menufuel:handleFullLXang'); // eslint-disable-line
        }
        catch (e) {
        }
    }
    handleFullLDau() {
        try {
            mp.trigger('client:menufuel:handleFullLDau'); // eslint-disable-line
        }
        catch (e) {
        }
    }
    handleFullLDien() {
        try {
            mp.trigger('client:menufuel:handleFullLDien'); // eslint-disable-line
        }
        catch (e) {
        }
    }
    handleFullLXangMayBay() {
        try {
            mp.trigger('client:menufuel:handleFullLXangMayBay'); // eslint-disable-line
        }
        catch (e) {
        }
    }
    render() {

        if (!this.state.show) {
            return null;
        }
        return (
            <div className={cx('container')}>
                <div className={cx('petrol')}>
                    <div className={cx('exitp')} onClick={(status) => this.handleExit(status)}>THOÁT</div>
                    <div className={cx('petrol_center')}>
                        <div className={cx('block_waves')}>
                            <div className={cx('wave1')} />
                            <div className={cx('wave2')} />
                        </div>
                        <div className={cx('petrol_name_block')}>
                            <div className={cx('petrol_name')}>ĐỊA CHỈ</div>
                            <div className={cx('petrol_street')}>{this.state.street}</div>
                        </div>
                        <div className={cx('petrol_info_block')}>
                            <div style={{ marginLeft: '30vh' }} className={cx('block_petrol_price')}>
                                <div className={cx('petrol_price_desc')}>GIÁ 1 LÍT DẦU</div>
                                <div className={cx('petrol_count')}>{this.state.giaDau}</div>
                            </div>
                            <div style={{ marginLeft: '2vh' }} className={cx('block_petrol_price')}>
                                <div className={cx('petrol_price_desc')}>GIÁ 1 LÍT XĂNG</div>
                                <div className={cx('petrol_count')}>{this.state.giaXang}</div>
                            </div>
                        </div>
                        <div className={cx('petrol_fast_block')}>
                            <div className={cx('petrol_fast_name')}>LỰA CHỌN</div>
                            <div className={cx('petrol_fblock')} onClick={() => this.handleFullLXang()}>
                                <div className={cx('petrol_ico')}><i className="fa-solid fa-gas-pump"></i></div>
                                <div className={cx('pfblock_name')}>Đổ xăng</div>
                                <div className={cx('pfblock_desc')}>Bấm để đổ</div>
                            </div>
                            <div className={cx('petrol_fblock2')} onClick={() => this.handleFullLDau()}>
                                <div className={cx('petrol_ico')}><i className="fa-solid fa-gas-pump"></i></div>
                                <div className={cx('pfblock_name')}>Đổ dầu</div>
                                <div className={cx('pfblock_desc')}>Bấm để đổ</div>
                            </div>
                        </div>
                        <div className={cx('petrol_buy')}>MARYLAND ROLEPLAY</div>
                    </div>
                </div>

            </div>

        )
    }
}

export default MenuFuel;
