/* eslint-disable */
import React from 'react';

import classNames from 'classnames/bind'
import styles from './css/style.module.css'
import EventManager from "../../../EventManager";

let cx = classNames.bind(styles);

class Lottery extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,

        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Lottery.jsx', error, errorInfo); // eslint-disable-line
    }

    componentDidMount() {
        EventManager.addHandler('Lottery', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            }
        })
    }

    componentWillUnmount() {
        EventManager.removeHandler('lottery');
    }

    handleExit(status) {
        try {
            mp.trigger('client:lottery:closeLottery', status); // eslint-disable-line
        }
        catch (e) {
            console.log(e);
        }
    }

    render() {
        if (!this.state.show) {
            return null;
        }
        return (
            <React.Fragment>
                <div className={cx('container')}>
                    <span>efsegs</span>
                </div>
            </React.Fragment>
        )
    }
}

export default Lottery;
