/* eslint-disable */
import React from 'react';

import './css/main.css'
import EventManager from "../../EventManager";
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";
import Job from './components/Job';
import Economy from './components/Economy';
import Tax from './components/Tax';

class Cityhallmenu extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
            visible: false,
            getRegister: false,



        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Cityhallmenu.jsx', error, errorInfo); // eslint-disable-line
    }

    componentDidMount() {
        EventManager.addHandler('Cityhallmenu', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            } else if (value.type === 'updateItems') {
                this.setState({ getJob: value.Job });
            }

        })
    }


    componentWillUnmount() {
        EventManager.removeHandler('Cityhallmenu');
    }

    handleExit(status) {
        try {
            mp.trigger('client:cityhallmenu:closeMenuCityHall', status); // eslint-disable-line
        }
        catch (e) {
            console.log(e);
        }
    }

    CreateCrew() {
        try {
            mp.trigger('client:cityHall:createCrew'); // eslint-disable-line

        } catch (e) {
            // console.log(`Nghỉ việc`)
        }
    };



    render() {
        if (!this.state.show) {
            return null;
        }
        return (
            <Router>
                <React.Fragment>
                    <div className='container-menu'>
                        <div className="box-menu">
                            <div className="header">
                                <Link to="/" className="logo">
                                    <span >CITYHALL REDUX</span>
                                </Link>
                                <Link to='/job' className="job">
                                    <span>Công việc</span>
                                </Link>
                                <Link to='/Economy' className="license">
                                    <span>Kinh tế</span>
                                </Link>
                                <Link to='/tax' className="tax">
                                    <span>Thuế</span>
                                </Link>
                                <div className='btn-exit'>
                                    <i className="fa-solid fa-xmark" onClick={(status) => this.handleExit(status)}></i>
                                </div>

                            </div>

                            <div className='body'>
                                <Switch>
                                    <Route exact path="/">
                                        <div className='content'>
                                            <div className='img'>
                                                <img src={`https://img.gta5-mods.com/q75/images/new-lapd-mission-row-police-station/fa3a17-LA.jpg`} />
                                            </div>
                                            <div className='title-body'>
                                                <h2>CITY HALL</h2>
                                                <span>Trụ sở của trung tâm thành phố <p>REDUX ROLEPLAY</p> nơi mà bạn có thể đăng ký công dân, tìm việc làm và nhiều thứ khác</span>
                                            </div>
                                        </div>
                                        <div className='btn-select'>

                                            <div className='btn-family'>
                                                <button onClick={() => this.CreateCrew()}>TẠO FAMILY</button>
                                            </div>


                                        </div>




                                    </Route>
                                    <Route exact path="/job">
                                        <Job />
                                    </Route>
                                    <Route exact path="/Economy">
                                        <Economy />
                                    </Route>
                                    <Route exact path="/tax">
                                        <Tax />
                                    </Route>
                                </Switch>
                            </div>
                        </div>
                    </div>
                </React.Fragment>
            </Router>

        )
    }
}

export default Cityhallmenu;
