import React from 'react';
import EventManager from "../../../EventManager";
import { BrowserRouter as Router, Route, NavLink, Switch } from "react-router-dom";
import classNames from 'classnames/bind'
import styles from '../css/character.module.css'

let cx = classNames.bind(styles);

const Character = ({ generalData }) => {
    return (
        <div className={cx('character')}>
            <div className={cx('box')}>
                <p>Tên nhan vật: <span>{generalData.nickname}</span></p>
                <p>Giới tính: <span>{generalData.gender}</span></p>
                <p>Tuổi :<span>{generalData.age} tuổi</span></p>
                <p>Người yêu: <span>{generalData.husband}</span></p>

            </div>
            <div className={cx('box')}>
                <p>EXP : <span>{generalData.exp}/{generalData.nextEXP}</span></p>
                <p>Cấp độ: <span>{generalData.levelWork}</span></p>
                <p>Quốc tịch: <span>{generalData.status}</span></p>
                <p>Số giờ chơi: <span>{generalData.hours} giờ</span></p>

            </div>
            <div className={cx('box')}>
                <p>Tiền trong người : <span>{generalData.pocketmoney}</span></p>
                <p>Tiền trong bank: <span>{generalData.cardmoney}</span></p>
                <p>Lần cuối đăng nhập: <span>{generalData.lastlogin}</span></p>
                <p>Tài khoản tạo ngày:: <span>{generalData.created} giờ</span></p>
            </div>
            <div className={cx('box')}>
                <p>Tổ chức : <span>{generalData.fraction}</span></p>
                <p>Cấp bậc: <span>{generalData.rankFaction}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Bảo hiểm y tế : <span>{generalData.medPercent}</span></p>
                <p>Ngày hết hạn: <span>{generalData.medDate}</span></p>
                <p>Đã sử dụng được: <span>{generalData.medActive} lần</span></p>
            </div>
            <div className={cx('box')}>
                <p>Loại VIP : <span>{generalData.statutVip === 2 ? <span>LIGHT</span> : <span>HARD</span>}</span></p>
                <p>Ngày hết hạn: <span>{generalData.medDate}</span></p>
                <p>Độ đói : <span>{generalData.eatStatus}</span></p>
                <p>Độ khát: <span>{generalData.drinkStatus}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Công việc : <span>{
                    generalData.infoJob === 1 ? <span>Nông dân</span> :
                        generalData.infoJob === 2 ? <span>Thợ xây dựng</span> :
                            generalData.infoJob === 3 ? <span>Nhiếp ảnh gia</span> :
                                generalData.infoJob === 4 ? <span>Người đưa thư</span> :
                                    generalData.infoJob === 5 ? <span>Thợ cơ khí</span> :
                                        generalData.infoJob === 6 ? <span>Tài xế xe BUS 1</span> :
                                            generalData.infoJob === 7 ? <span>Tài xế xe BUS 2</span> :
                                                generalData.infoJob === 8 ? <span>Tài xế xe BUS 3</span> :
                                                    generalData.infoJob === 9 ? <span>Tài xế TAXI</span> :
                                                        generalData.infoJob === 10 ? <span>Giao tiền</span> :
                                                            generalData.infoJob === 11 ? <span>Người giao hàng</span> :
                                                                generalData.infoJob === 12 ? <span>Cha sứ</span> :
                                                                    generalData.infoJob === 13 ? <span>Thợ mỏ</span> :
                                                                        generalData.infoJob === 14 ? <span>Lâm tặc</span> :
                                                                            <span>Thất nghiệp</span>
                }</span></p>
                <p>WORD-ID : <span>{generalData.workLic}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Bằng lái A : <span>{generalData.aLic}</span></p>
                <p>Ngày được cấp: <span>{generalData.aLicCreate}</span></p>
                <p>Ngày hết hạn: <span>{generalData.aLicEnd}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Bằng lái B : <span>{generalData.bLic}</span></p>
                <p>Ngày được cấp: <span>{generalData.bLicCreate}</span></p>
                <p>Ngày hết hạn: <span>{generalData.bLicEnd}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Bằng lái C : <span>{generalData.cLic}</span></p>
                <p>Ngày được cấp: <span>{generalData.cLicCreate}</span></p>
                <p>Ngày hết hạn: <span>{generalData.cLicEnd}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Bằng lái tàu : <span>{generalData.shipLic}</span></p>
                <p>Ngày được cấp: <span>{generalData.shipLicCreate}</span></p>
                <p>Ngày hết hạn: <span>{generalData.shipLicEnd}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Bằng lái máy bay : <span>{generalData.airLic}</span></p>
                <p>Ngày được cấp: <span>{generalData.airLicCreate}</span></p>
                <p>Ngày hết hạn: <span>{generalData.airLicEnd}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Giấy phép vũ khí : <span>{generalData.gunLic}</span></p>
                <p>Ngày được cấp: <span>{generalData.medDate}</span></p>
                <p>Ngày hết hạn: <span>{generalData.medDate}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Giấy phép vận tải : <span>{generalData.taxiLic}</span></p>
                <p>Ngày được cấp: <span>{generalData.taxiLicCreate}</span></p>
                <p>Ngày hết hạn: <span>{generalData.taxiLicEnd}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Giấy phép kinh doanh : <span>{generalData.bizLic}</span></p>
                <p>Ngày được cấp: <span>{generalData.bizLicCreate}</span></p>
                <p>Ngày hết hạn: <span>{generalData.bizLicEnd}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Giấy phép vũ khí : <span>{generalData.gunLic}</span></p>
                <p>Ngày được cấp: <span>{generalData.gunLicCreate}</span></p>
                <p>Ngày hết hạn: <span>{generalData.gunLicEnd}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Giấy phép câu cá : <span>{generalData.fishLic}</span></p>
                <p>Ngày được cấp: <span>{generalData.fishLicCreate}</span></p>
                <p>Ngày hết hạn: <span>{generalData.fishLicEnd}</span></p>
            </div>
            <div className={cx('box')}>
                <p>Đã đi bộ được: <span>{generalData.daDiBo} km</span> </p>
                <p>Đã chạy được: <span>{generalData.daChay} km</span></p>
                <p>Đã bắn được: <span>{generalData.daBan} lần</span></p>
            </div>
            <div className={cx('box')}>
                <p>Đã ở tù được: <span>{generalData.daOtU} lần</span></p>
                <p>Đã chết được: <span>{generalData.daChet} lần</span></p>
                <p>Đã giết người được: <span>{generalData.daGietNguoi} người</span></p>
            </div>
            <div className={cx('box')}>
                <p>Đã phạm tội được: <span>{generalData.daPhamToi} lần</span></p>
                <p>Đã lái xe được: <span>{generalData.daLaiXe} km</span></p>
                <p>Đã bơi được: <span>{generalData.daBoi} km</span></p>
            </div>
        </div>

    )
}
export default Character
