
import React from 'react';
import EventManager from "../../../src/EventManager";
import { BrowserRouter as Router, Route, NavLink, Switch } from "react-router-dom";
import classNames from 'classnames/bind'
import styles from './css/menubatdongsan.module.css'

let cx = classNames.bind(styles);

class Batdongsan extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,

        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Batdongsan.jsx', error, errorInfo); // eslint-disable-line
    }
    componentDidMount() {
        EventManager.addHandler('Batdongsan', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            } else if (value.type === "updateValues") {
                this.setState({ show: value.isShow });

            } else return;
        })
    }
    componentWillUnmount() {
        EventManager.removeHandler('Batdongsan');
    }

    handleExit(status) {
        try {
            mp.trigger('client:menufuel:close', status); // eslint-disable-line
        }
        catch (e) {
            console.log(e);
        }
    }

    render() {

        if (!this.state.show) {
            return null;
        }
        return (
            <Router>
                <div className={cx('container')}>
                    <div className={cx('box')}>
                        <div className={cx('header')}>
                            <NavLink to='/taphoa' className={cx('navbar')} activeClassName={cx("active")}>
                                <span>CỬA HÀNG TẠP HÓA</span>
                            </NavLink>
                            <NavLink to='/quanao' className={cx('navbar')} activeClassName={cx("active")}>
                                <span>CỬA HÀNG QUẦN ÁO</span>
                            </NavLink>
                            <NavLink to='/sung' className={cx('navbar')} activeClassName={cx("active")}>
                                <span>CỬA HÀNG SÚNG</span>
                            </NavLink>
                            <NavLink to='/phuongtien' className={cx('navbar')} activeClassName={cx("active")}>
                                <span>CỬA HÀNG PHƯƠNG TIỆN</span>
                            </NavLink>
                            <NavLink to='/thuexe' className={cx('navbar')} activeClassName={cx("active")}>
                                <span>CỬA HÀNG THUÊ PHƯƠNG TIỆN</span>
                            </NavLink>
                            <NavLink to='/bank' className={cx('navbar')} activeClassName={cx("active")}>
                                <span>NGÂN HÀNG TƯ NHÂN</span>
                            </NavLink>
                            <div className={cx('navbar')}>
                                <i className="fa-solid fa-xmark"></i>
                            </div>
                        </div>
                        <div className={cx('body')}>
                            <Switch>
                                <Route path='/quanao'>
                                    <div className={cx('card')}>
                                        <div className={cx('img')}>
                                            <img src={`https://pbs.twimg.com/media/EtdDuvlXYAAeXUc.jpg:large`} />
                                        </div>
                                        <p>Tên cửa hàng:<span>SUB URBAN</span></p>
                                        <p>Chủ sở hữu:<span>Chưa có</span></p>
                                        <p>Giá:<span>20000000$</span></p>
                                        <p>Mức thuế thu nhập:<span>100$</span></p>
                                        <div className={cx('btn')}>
                                            <button>MUA</button>
                                        </div>
                                    </div>
                                    <div className={cx('card')}>
                                        <div className={cx('img')}>
                                            <img src={`https://i.ytimg.com/vi/3SPnIfjBoNQ/maxresdefault.jpg`} />
                                        </div>
                                        <p>Tên cửa hàng:<span>SUB URBAN 2</span></p>
                                        <p>Chủ sở hữu:<span>Derry Nguyen</span></p>
                                        <p>Giá:<span>20000000$</span></p>
                                        <p>Mức thuế thu nhập:<span>100$</span></p>
                                        <div className={cx('btn')}>
                                            <button>CHI TIẾT</button>
                                        </div>
                                    </div>
                                </Route>
                            </Switch>
                        </div>

                    </div>
                </div>
            </Router>


        )
    }
}

export default Batdongsan;
