
import React from 'react';
import EventManager from "../../EventManager";
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";
import classNames from 'classnames/bind'
import styles from './css/main.module.css'
import Car from './images/test.jpg'

let cx = classNames.bind(styles);

class Infovehicle extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
            fuelType: 'Xăng',
            fuelFull: 100,
            fuelMin: 5,
            vName: 'BWM M4 G82 Competition',
            s_km: 57.75,
            price: 12000000,
            Plate: '223THFG5',
            Model: 'Sport',
            stockFull: 50,
            vEngine: 100,
            vBugi: 99,
            vWheel: 77,
            vElectric: 99,
            vFuel: 99,
            vPhanh: 99,
            fuel: 50,
            fuelIcon: 'L'

        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Infovehicle.jsx', error, errorInfo); // eslint-disable-line
    }
    componentDidMount() {
        EventManager.addHandler('Infovehicle', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            } else if (value.type === "updateValues") {
                this.setState({ show: value.isShow });
                this.setState({ fuelType: value.fuelType });
                this.setState({ fuelFull: value.fuelFull });
                this.setState({ fuelMin: value.fuelMin });
                this.setState({ vName: value.vName });
                this.setState({ s_km: value.s_km });
                this.setState({ price: value.price });
                this.setState({ Plate: value.Plate });
                this.setState({ Model: value.Model });
                this.setState({ stockFull: value.stockFull });
                this.setState({ vEngine: value.vEngine });
                this.setState({ vBugi: value.vBugi });
                this.setState({ vWheel: value.vWheel });
                this.setState({ vElectric: value.vElectric })
                this.setState({ vFuel: value.vFuel })
                this.setState({ vPhanh: value.vPhanh })
                this.setState({ fuel: value.fuel })
                this.setState({ fuelIcon: value.fuelIcon })

            } else return;

        })
    }
    componentWillUnmount() {
        EventManager.removeHandler('Infovehicle');
    }
    handleExit(status) {
        try {
            mp.trigger('client:Infovehicle:close', status); // eslint-disable-line
        }
        catch (e) {
            console.log(e);
        }
    }
    render() {
        if (!this.state.show) {
            return null;
        }
        return (
            <Router>
                <div className={cx("container")}>
                    <div className={cx('box-vehicle')}>
                        <div className={cx('title')}>
                            <span>Thông tin phương tiện</span>
                        </div>
                        <div className={cx('img-car')}>
                            <img src={`https://majestic-web-thanhhaicute.vercel.app/ui/carsv/640/${this.state.vName.toLowerCase()}.jpg`} />

                        </div>
                        <div className={cx('infoone')}>
                            <div className={cx('price')}>
                                <span><i className="fa-solid fa-tag"></i>${this.state.price}</span>
                            </div>
                            <div className={cx('numberkm')}>
                                <span><i className="fa-solid fa-road"></i>{this.state.s_km}km</span>
                            </div>
                        </div>
                        <div className={cx('namecar')}>
                            <span>{this.state.vName}</span>
                        </div>
                        <div className={cx('infotwo')}>
                            <div className={cx('plate')}>
                                <p>Biển số:<span>{this.state.Plate}</span> </p>
                            </div>
                            <div className={cx('dateresplate')}>
                                <p>Ngày đăng ký:<span>UPDATE</span> </p>
                            </div>
                            <div className={cx('maxfuel')}>
                                <p><i className="fa-solid fa-gas-pump"></i> {this.state.fuel}{this.state.fuelIcon} </p>
                            </div>
                        </div>

                        <div className={cx('status')}>
                            <span>Trạng thái phương tiện:</span>
                            <div className={cx('top')}>
                                <div><i className="fa-solid fa-gear"></i>Động cơ<span> {this.state.vEngine}%</span></div>
                                <div><i className="fa-solid fa-bolt"></i>Bu-gi:<span> {this.state.vBugi}%</span></div>
                                <div><i className="fa-solid fa-dharmachakra"></i>Bánh xe:<span> {this.state.vWheel}%</span></div>
                            </div>
                            <div className={cx('bottom')}>
                                <div><i className="fa-solid fa-crutch"></i>Đánh điện<span> {this.state.vElectric}%</span></div>
                                <div><i className="fa-solid fa-minimize"></i>Bình xăng:<span> {this.state.vFuel}%</span></div>
                                <div><i className="fa-solid fa-dumbbell"></i>Phanh xe:<span> {this.state.vPhanh}%</span></div>
                            </div>
                        </div>

                        <div className={cx('infomation')}>
                            <span>Thông tin phương tiện:</span>
                            <div className={cx('top')}>
                                <div>Loại nhiên liệu:<span> {this.state.fuelType}</span></div>
                                <div>Dung tích bình:<span> {this.state.fuelFull}L</span></div>

                            </div>
                            <div className={cx('bottom')}>
                                <div>Mẫu phương tiện:<span>{this.state.Model}</span></div>
                                <div>Khối lượng cốp:<span> {this.state.stockFull}KG</span></div>
                                <div>Mức tiêu thụ:<span> {this.state.fuelMin}{this.state.fuelIcon}</span></div>
                            </div>
                        </div>
                        <div className={cx('btn')} onClick={(status) => this.handleExit(status)}>
                            <button>ĐÓNG</button>
                        </div>
                    </div>
                </div>


            </Router >
        )
    }
}

export default Infovehicle;
