import React from 'react';

import './css/style.css'
import EventManager from "../../../EventManager";


class Roullete extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
            visible: false,
            reels : [
                {
                    id:1,
                    img:'https://beco.vn/wp-content/uploads/2019/09/peach.png'
                },
                {
                    id:2,
                    img:'https://freesvg.org/img/Gerald-G-Simple-Fruit-FF-Menu-5.png'
                },
                {
                    id:3,
                    img:'https://upload.wikimedia.org/wikipedia/commons/f/fd/Grape_icon.png'
                },
                {
                    id:4,
                    img:'https://rainierfruit.com/wp-content/uploads/2021/10/Rainier-Fruit-Pears.png'
                },
                {
                    id:5,
                    img:'https://www.clipartmax.com/png/full/451-4514529_orange-fruit-clipart-2-orange-qu%E1%BA%A3-qu%C3%BDt.png'
                },
                {
                    id:6,
                    img:'https://www.texturecan.com/img/textures/food_0001/food_0001_sphere_600.png'
                },
                {
                    id:7,
                    img:'https://www.tinhdauthaoduoc.net/files/tin/87/png/tinh-dau-vo-buoi-grapefruit-hay-pomelo.png'
                }

            ]

        }
    }
    
    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Roullete.jsx', error, errorInfo); // eslint-disable-line
    }

    componentDidMount() {
        EventManager.addHandler('Roullete', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            }
        })
    }

    componentWillUnmount() {
        EventManager.removeHandler('Roullete');
    }

    handleExit(status) {
        try {
            mp.trigger('client:lottery:closeLottery', status); // eslint-disable-line
        }
        catch (e) {
            console.log(e);
        }
    }
    handleSpin(e){
        setTimeout(function(){
            
        },3000)
    }

    render() {
        if (!this.state.show) {
            return null;
        }
        return (
            <>
                <div className='container-roullete'>
                    <div className="box-roullete">
                        <div className="box-1">
                            <img src={`https://andyhoffman.codes/random-assets/img/slots/sprite5.png`} alt="" />
                        </div>
                        <div className="box-2">
                            <img src={`https://andyhoffman.codes/random-assets/img/slots/sprite5.png`} alt="" />

                        </div>
                        <div className="box-3">
                            <img src={`https://andyhoffman.codes/random-assets/img/slots/sprite5.png`} alt="" />

                        </div>

                    </div>
                    <div className="btn-play-roullete">
                        <button onClick={() => this.handleSpin()}>Quay</button>
                    </div>
                </div>
            </>
        )
    }
}

export default Roullete;
